






















import Vue from 'vue'
export default Vue.extend({
    name: 'ExperimentLearnIntro',
    data: () => ({
    }),
    async mounted() {
        if (!this.connected)
            return await this.$router.push('/experiment');
    },
    computed: {
        connected() {
            return this.$store.state.connected;
        },
        randomFlags() {
            return this.$store.state.randomFlags;
        },
        subsetId(): number {
            return +this.$route.params.subsetId;
        },
    },
})
